.hero {
  background-color: #f6f6f6;
  text-align: center;
  font-family: "Roboto";
  padding-left: 34px;
  padding-right: 34px;
}
.hero__content {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.hero .section-title {
  height: 68px;
  line-height: 28.13px;
  margin-top: 39px;
}
.hero__text {
  font-size: 18px;
  font-weight: 300;
  line-height: 21.09px;
}
.hero__button {
  margin-top: 27px;
  margin-bottom: 57px;
}